* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html,
body {
  height: 100%; }

html {
  font-family: Arial, Helvetica, sans-serif;
  background-image: url(/static/media/hospital.2b101236.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 10px; }

body {
  display: flex;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(/static/media/hospital.2b101236.jpg);
  min-width: 1200px;
  min-height: 713px; }

#root {
  display: grid;
  place-items: center center; }

::-webkit-scrollbar {
  display: none; }

.container {
  border-radius: 5px;
  flex-direction: row;
  display: flex; }
  .container .content-left {
    z-index: 2;
    float: left;
    margin-left: -200px !important;
    background-size: cover;
    background-position: 0 0;
    background-position: initial;
    background-repeat: no-repeat;
    background-image: url(/static/media/hospital.2b101236.jpg);
    height: 100vh;
    flex: 1 1;
    margin-right: 20px; }
  .container .content-right {
    z-index: 2;
    border-radius: 5px;
    align-self: center;
    min-width: 25vw;
    margin: 0 auto;
    height: 80vh;
    background-color: #fff;
    padding: 40px 40px 25px; }
    .container .content-right .title {
      font-size: 25px;
      color: #000333;
      border-bottom: 1px solid #E6E6E6;
      padding-bottom: 20px;
      margin-bottom: 30px;
      font-weight: 700; }
    .container .content-right .form-group {
      flex-direction: column;
      display: flex; }
      .container .content-right .form-group .form-control {
        margin: 0 !important; }
      .container .content-right .form-group label {
        font-weight: 700;
        font-style: normal;
        color: #c9c9c9;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-bottom: 6px;
        font-size: 14px;
        font-family: sans-serif;
        font-style: italic; }
      .container .content-right .form-group .err {
        color: red;
        padding: 10px 0;
        font-size: small;
        font-weight: 700; }
      .container .content-right .form-group .hint {
        letter-spacing: 0.5;
        font-size: 12px;
        color: #aaa;
        font-weight: 500;
        font-style: italic; }
      .container .content-right .form-group .button {
        height: 50px;
        font-size: 16px;
        margin-top: 50px; }
      .container .content-right .form-group .forget {
        position: absolute;
        bottom: 0;
        padding-bottom: 20px; }
        .container .content-right .form-group .forget a {
          font-family: "Inter", sans-serif;
          font-size: 13px;
          color: #aaa;
          font-weight: 700;
          line-height: 1.75;
          text-underline-offset: 2px; }
          .container .content-right .form-group .forget a:hover {
            color: red !important; }
      .container .content-right .form-group .other-media {
        flex-direction: row;
        display: flex;
        margin-top: 10px;
        align-self: center; }
        .container .content-right .form-group .other-media .ios-app {
          background: url(/static/media/app-store-badge-black.6695bc03.svg) center center no-repeat;
          width: 110px;
          height: 32px;
          cursor: pointer;
          background-size: cover;
          margin-right: 25px;
          margin-left: 10px; }
        .container .content-right .form-group .other-media .android-app {
          background: url(/static/media/google-play-badge.4b8d695d.svg) center no-repeat;
          width: 110px;
          height: 32px;
          cursor: pointer;
          background-size: cover; }
      .container .content-right .form-group .mobile {
        text-align: center;
        font-size: 14px;
        color: #aaa;
        font-weight: 400;
        margin-top: 20px; }

#search-container {
  display: flex;
  align-items: center;
  grid-area: search-container;
  background: #0048AA;
  padding: 0 20px;
  border-radius: 10px 0 0 0;
  box-shadow: 0 1px 3px -1px #000000bf;
  z-index: 1; }
  #search-container input {
    width: 0;
    flex: 1 0;
    color: #EEE;
    outline: none;
    font-weight: bold;
    border-radius: 2px;
    height: 30px;
    border: 0;
    padding-left: 48px;
    padding-right: 20px;
    font-size: 1.4rem;
    background: url(/static/media/search.44d3b27b.svg) no-repeat #ffffff4d;
    background-position: 15px center;
    background-size: 20px 20px; }
  #search-container input::-webkit-input-placeholder {
    color: #DDD;
    font-weight: bold; }
  #search-container input:-ms-input-placeholder {
    color: #DDD;
    font-weight: bold; }
  #search-container input::placeholder {
    color: #DDD;
    font-weight: bold; }

.primary-button {
  flex: 0 0 auto;
  background: #0048AA;
  border: 1px solid #003781;
  color: white;
  padding: 12px;
  border-radius: 5px;
  font-size: 1.4rem;
  cursor: pointer;
  outline: none; }
  .primary-button:disabled {
    background: #778eaf;
    border: 1px solid #747d8f; }

#no-coversation-layout {
  flex: 1 0;
  display: flex;
  flex-direction: column; }
  #no-coversation-layout #no-conversation-content {
    flex: 1 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0 0 10px 10px; }
  #no-coversation-layout h2 {
    color: #0048AA;
    font-size: 3.5rem;
    margin-bottom: 2rem; }
  #no-coversation-layout p {
    font-size: 2rem; }
  #no-coversation-layout p:last-of-type {
    margin-bottom: 1.5rem; }

.conversation {
  display: grid;
  grid-template-columns: 40px 1fr -webkit-max-content;
  grid-template-columns: 40px 1fr max-content;
  grid-gap: 10px;
  color: #FFF;
  font-size: 1.3rem;
  border-bottom: 1px solid #003781;
  padding: 20px 20px 20px 15px; }
  .conversation.selected, .conversation:hover {
    background: #003781; }
  .conversation:hover {
    cursor: pointer; }
  .conversation > img {
    grid-row: span 2;
    height: 40px;
    width: 40px;
    border-radius: 100%; }
  .conversation .title-text {
    font-weight: bold;
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis; }
  .conversation .created-date {
    color: #DDD;
    white-space: nowrap;
    font-size: 1rem; }
  .conversation .conversation-message {
    grid-column: span 2;
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis; }

#conversation-list {
  grid-area: conversation-list;
  background: #0048AA;
  overflow-y: scroll; }

#new-message-container {
  display: grid;
  grid: 40px / 40px;
  align-content: center;
  grid-area: new-message-container;
  background: #0048AA;
  border-top: 1px solid #000c1c;
  border-radius: 0 0 0 10px;
  padding: 0 15px; }
  #new-message-container button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #EEE;
    border-radius: 100%;
    color: #003781;
    text-decoration: none;
    font-size: 3.6rem;
    line-height: 3.6rem;
    outline: none;
    border: none;
    cursor: pointer; }
    #new-message-container button img {
      width: 50px;
      height: 50px; }

.trash-logo {
  stroke: #0048AA;
  cursor: pointer; }

#chat-title {
  display: grid;
  grid: 36px / 1fr 36px;
  align-content: center;
  align-items: center;
  grid-area: chat-title;
  background: #EEE;
  color: #0048AA;
  font-weight: bold;
  font-size: 2.0rem;
  border-radius: 0 10px 0 0;
  box-shadow: 0 1px 3px -1px #2e2e2e;
  padding: 0 20px;
  z-index: 1; }

.message-row {
  display: grid;
  grid-template-columns: 70%;
  margin-bottom: 20px; }
  .message-row > .message-content {
    display: grid; }
    .message-row > .message-content > img {
      border-radius: 100%;
      grid-row: span 2;
      width: 48px;
      height: 48px; }
    .message-row > .message-content > .message-time {
      font-size: 1.3rem;
      color: #777777; }
    .message-row > .message-content > .message-text {
      padding: 9px 14px;
      font-size: 1.6rem;
      margin-bottom: 5px; }
  .message-row.you-message {
    justify-content: end; }
    .message-row.you-message > .message-content {
      justify-items: end; }
      .message-row.you-message > .message-content > .message-text {
        background: #0048AA;
        color: #FFF;
        border: 1px solid #0048AA;
        border-radius: 14px 14px 0 14px; }
  .message-row.other-message {
    justify-items: start; }
    .message-row.other-message > .message-content {
      grid-template-columns: 48px 1fr;
      grid-column-gap: 15px; }
      .message-row.other-message > .message-content > .message-text {
        background: #EEE;
        color: #111;
        border: 1px solid #cccccc;
        border-radius: 14px 14px 14px 0; }

#chat-message-list {
  grid-area: chat-message-list;
  display: flex;
  flex-direction: column-reverse;
  padding: 0 20px;
  overflow-y: scroll; }
  #chat-message-list::-webkit-scrollbar {
    display: block;
    width: 6px;
    height: 8px;
    cursor: pointer; }
  #chat-message-list::-webkit-scrollbar-track {
    background: #ddd; }
  #chat-message-list::-webkit-scrollbar-thumb {
    background: #bdbdbd; }
  #chat-message-list .message-row:last-child {
    margin-top: 20px; }

#chat-form {
  display: flex;
  align-items: center;
  grid-area: chat-form;
  background: #EEE;
  border-radius: 0 0 10px 0;
  border-top: 1px solid #cfcfcf;
  padding-left: 42px;
  padding-right: 22px; }
  #chat-form input {
    flex: 1 0;
    outline: none;
    padding: 15px;
    border: 2px solid #dddddd;
    border-right: none;
    color: #333300;
    border-radius: 6px 0 0 6px;
    font-size: 1.4rem;
    margin-left: 15px; }
  #chat-form button {
    height: 51px;
    flex: 0 0 90px;
    border-radius: 0 6px 6px 0; }

.attachment-logo {
  fill: #0048AA;
  cursor: pointer;
  width: 32px;
  height: 32px;
  enable-background: new 0 0 512.001 512.001; }

#chat-container {
  display: grid;
  grid: 'search-container chat-title' 71px 'conversation-list chat-message-list' 1fr 'new-message-container chat-form' 78px / 275px 1fr;
  min-width: 1000px;
  max-width: 1000px;
  width: 100%;
  height: calc(713px - 30px);
  background: white;
  border-radius: 10px; }

